<template>
  <div>
  <h2>Search operators</h2>

  <p>The search field utilizes mongodb's <b>$search</b> operator. As result, the query syntax is the same. You can learn more about in mongodb's documentation:
  <a href="https://docs.mongodb.com/manual/reference/operator/query/text/#search-field" target="_blank">https://docs.mongodb.com/manual/reference/operator/query/text/#search-field</a></p>

  <h4>Search hashtags</h4>
  <p>Put <code><strong>#</strong></code> in front of a word and enclose the word in double quotes (<code><strong>"</strong></code>). For example: <code><strong>"#bitquery"</strong></code>
  <a href="https://oyo.cash/result/IiNiaXRxdWVyeSI=/0">[see on Oyo]</a></p>

  <h4>Exclude words from your search</h4>
  <p>Put <code><strong>-</strong></code> in front of a word you want to leave out. For example, <code><strong>bitcoin -core</strong></code>
  <a href="https://oyo.cash/result/Yml0Y29pbiAtY29yZQ==/0">[see on Oyo]</a></p>

  <h4>Search protocol prefixes</h4>
  <p>Put <code><strong>prefix:</strong></code> in front of a prefix code. For example, <code><strong>prefix:6d02 cats</strong></code> <a href="https://oyo.cash/result/cHJlZml4OjZkMDIgY2F0cw==/0">[see on Oyo]</a></p>

  <h4>Search in apps</h4>
  <p>Put <code><strong>app:</strong></code> in front of the app identifier. For example, <code><strong>app:twetch love</strong></code> <a href="https://oyo.cash/result/YXBwOnR3ZXRjaCBsb3Zl/0">[see on Oyo]</a></p>

  <h4>Search from date</h4>
  <p>Put <code><strong>datetime:</strong></code> in front of date time (format: yyyyMMddHHmm). For example, <code><strong>prefix:status datetime:201904010000</strong></code> <a href="https://oyo.cash/result/cHJlZml4OnN0YXR1cyBkYXRldGltZToyMDE5MDQwMTAwMDA=/0">[see on Oyo]</a></p>

  <h4>Search for period</h4>
  <p> <i>The default period is 30 days.</i></p>
  <p>Put <code><strong>period:</strong></code> in front of the desired search period (in days). For example, <code><strong>prefix:status datetime:201907010000 period:90</strong></code> <a href="https://oyo.cash/result/cHJlZml4OnN0YXR1cyBkYXRldGltZToyMDE5MDcwMTAwMDAgcGVyaW9kOjkw/0">[see on Oyo]</a></p>


  <h4>Search transactions from (sender)</h4>
  <p>Put <code><strong>from:</strong></code> in front of the sender's address. For example <code><strong>from:17yvo2MY6DSX4JFJNWzEdav95EDE9PcWvx bitdb</strong></code> <a href="https://oyo.cash/result/ZnJvbToxN3l2bzJNWTZEU1g0SkZKTld6RWRhdjk1RURFOVBjV3Z4IGJpdGRi/0">[see on Oyo]</a></p>

  <h4>Search transactions excluding sender</h4>
  <p>Put <code><strong>notfrom:</strong></code> in front of the sender's address. For example <code><strong>notfrom:17yvo2MY6DSX4JFJNWzEdav95EDE9PcWvx bitdb</strong></code> <a href="https://oyo.cash/result/bm90ZnJvbToxN3l2bzJNWTZEU1g0SkZKTld6RWRhdjk1RURFOVBjV3Z4IGJpdGRi/0">[see on Oyo]</a></p>

  <h4>Search in h/b/s bitdb fields</h4>
  <p>Put bitdb field <code><strong>[b1, b2, ..., b15, h1, ..., h15, s1, ..., s15]:</strong></code> in front of the searched string. For example <code><strong>s1:$ description</strong></code> <a href="https://oyo.cash/result/czE6JCBkZXNjcmlwdGlvbg==/0">[see on Oyo]</a></p>
  <p>Multiple fields can be used in the visialization. For example For example <code><strong>h1:24 s2:echo s5:name genesis</strong></code> <a href="https://oyo.cash/result/aDE6MjQgczI6ZWNobyBzNTpuYW1lIGdlbmVzaXM=/0">[see on Oyo]</a></p>

  <h4>Exact match</h4>
  <p>Put a word or phrase inside quotes. For example, <code><strong>"bitcoin unlimited"</strong></code>.
  <a href="https://oyo.cash/result/ImJpdGNvaW4gdW5saW1pdGVkIg==/0">[see on Oyo]</a></p>

  <h4>Combining searches</h4>
  <p>
    Put a word or phrase inside quotes for mandatory match (AND). Use a word without quotes for optional match (OR). Examples:<br/>
    <code><strong>"moneybutton" "wordpress"</strong></code> (searches for "moneybutton AND wordpress") <a href="https://oyo.cash/result/Im1vbmV5YnV0dG9uIiAid29yZHByZXNzIg==/0">[see on Oyo]</a><br/>
    <code><strong>liquid lightning</strong></code> (searches for "liquid OR lightning") <a href="https://oyo.cash/result/bGlxdWlkIGxpZ2h0bmluZw==/0">[see on Oyo]</a><br/>
    <code><strong>"market" bull bear</strong></code> (searches for "market AND (market OR bull OR bear)") <a href="https://oyo.cash/result/Im1hcmtldCIgYnVsbCBiZWFy/0">[see on Oyo]</a>
  </p>
  </div>
</template>

<script>
  export default {
    name: 'HowToSearchOnOyo',
    components: {
    }
  };
</script>

<style>
