<template>
  <div class="grid">
    <HeaderNav :search="true" :searchText="searchText" v-on:searchTextInput="onSearch"/>
    <div class="s_results" v-if="loading === false">
      <div class="n_results">
        About {{ matchesCount }} results in {{ searchPeriod }} days
      </div>
      <div class="s_result" v-for="(item, index) in resultAllItems">
        {{ findText(item, index) }}
        <a class="title" :href= "getLink(item, resultOutIndex[index])">
          <span v-if="getTitle(item, resultOutIndex[index])">{{ getTitle(item, resultOutIndex[index]) }}</span>
          <span v-else>{{ resultMatchingStrings[index] | toTitle() }}</span>
        </a>
        <div class="content">
          <p class="s_url">
            <a class="s_url" :href= "getLink(item, resultOutIndex[index])">
              {{ item.tx.h }}
              <span v-if="getProtocolName(item, resultOutIndex[index])"> - {{ getProtocolName(item, resultOutIndex[index]) }}</span>
              <span v-if="resultApps[index]">  ({{ resultApps[index] }})</span>
            </a>
          </p>
          <div>
            <p class="s_detail">
              <span class="date" v-if="item.blk">{{ unixTimeToDatetime(item.blk.t) }} - </span><span class="date" v-else>Unconfirmed - </span><text-highlight :queries="highlightArray" :highlightStyle="'background: #FFFFFF;'" :highlightClass="'text_highlight'">{{ resultLongestStrings[index] }}</text-highlight>
            </p>
          </div>
        </div>
      </div>

      <!--
      <h3>
        Searches related to build this webpage
      </h3>
      <div class="r_searches">
        <ul id="l_left">
          <li>
            <a href="#" class="r_item">test 1</a>
          </li>
          <li>
            <a href="#" class="r_item">test 1</a>
          </li>
          <li>
            <a href="#" class="r_item">test 1</a>
          </li>
        </ul>
        <ul id="l_right">
          <li>
            <a href="#" class="r_item">test 1</a>
          </li>
          <li>
            <a href="#" class="r_item">test 1</a>
          </li>
        </ul>
      </div>
      //-->


      <div class="page_number" v-if="matchesCount">
        <div class="pagination">
        <paginate
          v-model="page"
          :pageCount="parseInt((matchesCount)/itemsPerPage) + Math.ceil((itemsPerPage%matchesCount)/itemsPerPage)"
          :pageRange="9"
          :clickHandler="changeSearchRoute"
          :prevText="'Previous'"
          :nextText="'Next'"
          :container-class="'pagination'"
          :page-class="'page-item'"
          :prev-class="'prev-item'"
          :next-class="'next-item'"
          :active-class="'active-item'"
          :hide-prev-next="true">
        </paginate>
        </div>
        <div class="showNextPeriod" v-if="matchesCount < (itemsPerPage + parseInt(skip))"><a @click="changeSearchDatetime(searchUnixTimestamp - (searchPeriod * 24 * 60 * 60))" href="javascript:void(0)">See {{searchPeriod}} days more</a></div>

      </div>
    </div>
    <div class="s_results" v-else>
      <div class="n_results">
        Loading...
      </div>
    </div>
    <Footer/>
  </div>
</template>

<script>
import Vue from 'vue'
import HeaderNav from '@/components/Header/Nav'
import Footer from '@/components/Footer'

export default {
  name: 'Result',
  props: {
    search: {type: String, required: true},
    skip: {type: String, required: true}
  },
  data () {
    return {
      blockExplorerRpc: window.blockExplorerRpc,
      searchPeriod: window.periodDefault,
      searchUnixTimestamp: null,
      highlightArray: [],
      orSearchArray: [],
      andSearchArray: [],
      offset: null,
      page: 1,
      itemsPerPage: null,
      matchesCount: null,
      searchText: Base64.decode(this.search),
      searchTextInput: Base64.decode(this.search),
      resultData: null,
      resultConfirmedItems: [],
      resultUnconfirmedItems: [],
      resultAllItems: [],
      resultLongestStrings: [],
      resultMatchingStrings: [],
      resultOutIndex: [],
      resultApps: [],
      bitcomPrefixes: [],
      bitcomProtocols: {},
      loading: true,
      errored: false
    }
  },
  head: function () {
    return {
      title: {
        inner: 'Search results for "' + this.searchText + '" on Bitcoin'
      },
      // Meta tags
      meta: [
        { name: 'application-name', content: 'Oyo.cash' },
        { name: 'description', content: 'Latest search results for "' + this.searchText + '" found on the Bitcoin blockchain', id: 'desc' },
        { name: 'twitter:title', content: 'Search results for "' + this.searchText + '" on Bitcoin' },
        { n: 'twitter:description', c: 'Latest search results for "' + this.searchText + '" found on the Bitcoin blockchain'},
        { itemprop: 'name', content: 'Search results for "' + this.searchText + '" on Bitcoin' },
        { itemprop: 'description', content: 'Latest search results for "' + this.searchText + '" found on the Bitcoin blockchain' },
        { property: 'og:title', content: 'Search results for "' + this.searchText + '" on Bitcoin' },
      ]
    }
  },
  components: {
    HeaderNav,
    Footer
  },
  mounted () {
    this.protocols = window.protocols

    this.offset = parseInt(this.skip)
    this.itemsPerPage = parseInt(resultsPerPage)
    this.page = parseInt(this.offset/this.itemsPerPage) + 1
    this.searchText = this.searchTextInput = Base64.decode(this.search)

    this.computeSearchArray()
    this.searchTheBlockchainForText()
    this.getMatchCount()
  },
  watch: {
    searchText: function (val) {
      this.$emit('updateHead')
    }
  },
  methods: {
    unixTimeToDatetime (unix_timestamp) {
      let a = new Date(parseInt(unix_timestamp) * 1000)
      let months = ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec']
      let year = a.getFullYear()
      let month = months[a.getMonth()]
      let date = a.getDate()
      let hour = ('0' + a.getHours()).substr(-2)
      let min = ('0' + a.getMinutes()).substr(-2)
      let time = date + ' ' + month + ' ' + year + ' ' + hour + ':' + min
      return time
    },
    oyoDatetimeToTimestamp (oyoDatetime) {
      let unixTimestamp = new Date(oyoDatetime.substring(0,4), parseInt(oyoDatetime.substring(4,6)) - 1, oyoDatetime.substring(6,8), oyoDatetime.substring(8,10), oyoDatetime.substring(10,12), 0, 0).getTime() / 1000
      return unixTimestamp
    },
    timestampToOyoDatetime (unix_timestamp) {
      let datetime = new Date(parseInt(unix_timestamp) * 1000).toISOString()
      let oyoDatetime = datetime.substring(0,4) + datetime.substring(5,7) + datetime.substring(8,10) + datetime.substring(11,13) + datetime.substring(14,16)
      return oyoDatetime
    },
    computeSearchArray () {
      let tmpSearchText = this.searchText
      while (tmpSearchText.match(/s(?:[1-9]|1[0-5]):\s*(\S+)\s*/u)) {
        let match = tmpSearchText.match(/(?:^|\s+)s([1-9]|1[0-5]):\s*(\S+)\s*/u)
        if (match && match[1] && match[2]) {
          let regex = new RegExp( '(?:^|\\s+)s' + match[1] + ':\\s*\\S+\\s*', 'gu' )
          tmpSearchText = tmpSearchText.replace(regex, ' ')
        }
      }
      while (tmpSearchText.match(/(?:^|\s+)h([1-9]|1[0-5]):\s*([0-9a-fA-F]+)\s*/u)) {
        let match = tmpSearchText.match(/(?:^|\s+)h([1-9]|1[0-5]):\s*([0-9a-fA-F]+)\s*/u)
        if (match && match[1] && match[2]) {
          let regex = new RegExp( '(?:^|\\s+)h' + match[1] + ':\\s*[0-9a-fA-F]+\\s*', 'gu' )
          tmpSearchText = tmpSearchText.replace(regex, ' ')
        }
      }
      while (tmpSearchText.match(/(?:^|\s+)b([1-9]|1[0-5]):\s*((?:[A-Za-z0-9+/]{4})*(?:[A-Za-z0-9+/]{2}==|[A-Za-z0-9+/]{3}=)?)\s*/u)) {
        let match = tmpSearchText.match(/(?:^|\s+)b([1-9]|1[0-5]):\s*((?:[A-Za-z0-9+/]{4})*(?:[A-Za-z0-9+/]{2}==|[A-Za-z0-9+/]{3}=)?)\s*/u)
        if (match && match[1] && match[2]) {
          let regex = new RegExp( '(?:^|\\s+)b' + match[1] + ':\\s*(?:[A-Za-z0-9+/]{4})*(?:[A-Za-z0-9+/]{2}==|[A-Za-z0-9+/]{3}=)?\\s*', 'gu' )
          tmpSearchText = tmpSearchText.replace(regex, ' ')
        }
      }
      if (tmpSearchText.includes("prefix:")) {
        tmpSearchText = tmpSearchText.replace(/(?:^|\s+)prefix:\s*[0-9a-zA-Z\.@]+\b\s*/gu, ' ')
      }
      if (tmpSearchText.includes("from:")) {
        tmpSearchText = tmpSearchText.replace(/(?:^|\s+)from:\s*\S+\s*/gu, ' ')
      }
      if (tmpSearchText.includes("notfrom:")) {
        tmpSearchText = tmpSearchText.replace(/(?:^|\s+)notfrom:\s*\S+\s*/gu, ' ')
      }
      if (tmpSearchText.includes("datetime:")) {
        tmpSearchText = tmpSearchText.replace(/(?:^|\s+)datetime:\s*\S+\s*/gu, ' ')
      }
      if (tmpSearchText.includes("period:")) {
        tmpSearchText = tmpSearchText.replace(/(?:^|\s+)period:\s*\S+\s*/gu, ' ')
      }
      tmpSearchText = tmpSearchText.trim()

      this.andSearchArray = []
      let pattern = /"(.*?)"/gu;
      let current;
      while(current = pattern.exec(tmpSearchText)) {
        this.andSearchArray.push(current[1].toLowerCase())
      }
      pattern = /'(.*?)'/gu;
      while(current = pattern.exec(tmpSearchText)) {
        this.andSearchArray.push(current[1].toLowerCase())
      }

      this.orSearchArray = []
      tmpSearchText = tmpSearchText.replace(/["',!?]/giu, '')
      this.orSearchArray = tmpSearchText.toLowerCase().split(' ')
      let orSearchArrayTmp = this.orSearchArray
      for (let orSIndex = 0; orSIndex < orSearchArrayTmp.length; ++orSIndex) {
        if (orSearchArrayTmp[orSIndex].endsWith('s') || orSearchArrayTmp[orSIndex].endsWith('ing')) {
          let rootString = orSearchArrayTmp[orSIndex].replace(/ings|ing|s$/gi, '')
          if(this.orSearchArray.indexOf(rootString) === -1) {
            this.orSearchArray.push(rootString)
          }
          if(this.orSearchArray.indexOf(rootString + 's') === -1) {
            this.orSearchArray.push(rootString + 's')
          }
          if(this.orSearchArray.indexOf(rootString + 'ing') === -1) {
            this.orSearchArray.push(rootString + 'ing')
          }
          if(this.orSearchArray.indexOf(rootString + 'ings') === -1) {
            this.orSearchArray.push(rootString + 'ings')
          }
        }
      }

      this.highlightArray = this.andSearchArray.concat(this.orSearchArray)
    },
    findText (item, itemIndex) {
      let outIndex = ''
      let outIndexBackup = ''
      let resultString = ''
      let largestString = ''

      for (let index = 0; index < item.out.length; ++index) {
        for (let sIndex = 0; sIndex < 16; ++sIndex) {
          let varName = 'item.out[index].s' + sIndex
          if (item.out[index] && typeof eval(varName) !== 'undefined') {
            if (this.orSearchArray.some(oItem => eval(varName).toLowerCase().includes(oItem))) {
              if (this.andSearchArray.every(aItem => eval(varName).toLowerCase().includes(aItem))) {
                if (resultString.length <= eval(varName).length) {
                  resultString = eval(varName)
                  outIndex = index
                }
                if (largestString.length <= eval(varName).length) {
                  largestString = eval(varName)
                  outIndexBackup = index
                }
              }
            }

            // Store in case no match is found
            if (largestString.length <= eval(varName).length) {
              if (/^[\x00-\xFF\u2000-\u20FF\u0400-\u04FF\u0600-\u06FF\u3000-\u30ff\u3400-\u4dbf\u4e00-\u9fff\uf900-\ufaff\uff66-\uff9f]+$/.test(eval(varName))) {
                largestString = eval(varName)
                outIndexBackup = index
              }
            }
          }
        }
      }

      if (resultString === '') {
        for (let index2 = 0; index2 < item.out.length; ++index2) {
          for (let sIndex2 = 0; sIndex2 < 16; ++sIndex2) {
            let varName = 'item.out[index2].s' + sIndex2
            if (item.out[index2] && typeof eval(varName) !== 'undefined') {
              if (this.andSearchArray.some(aItem2 => eval(varName).toLowerCase().includes(aItem2))) {
                if (resultString.length < eval(varName).length) {
                  outIndex = index2
                  resultString = eval(varName)
                }
              }
            }
          }
        }
      }

      if (resultString) {
        this.resultMatchingStrings[itemIndex] = resultString
      } else {
        this.resultMatchingStrings[itemIndex] = largestString
      }
      if (outIndex === '') {
        this.resultOutIndex[itemIndex] = outIndexBackup
      } else {
        this.resultOutIndex[itemIndex] = outIndex
      }
      if (this.resultOutIndex[itemIndex] === '') {
        this.resultOutIndex[itemIndex] = 0
      }

      this.resultLongestStrings[itemIndex] = largestString
    },
    changeSearchDatetime (unixTimestamp) {
      if (!unixTimestamp) {
        return
      }
      let oyoDatetime = this.timestampToOyoDatetime(unixTimestamp)
      if (this.searchTextInput.includes("datetime:")) {
        let match = this.searchTextInput.match(/(?:^|\s+)datetime:\s*(\S+)\s*/u)
        if (match && match[1]) {
          this.searchTextInput = this.searchTextInput.replace(/(?:^|\s+)datetime:\s*\S+\s*/gu, ' datetime: ' + oyoDatetime)
        }
      } else {
        this.searchTextInput = this.searchTextInput + ' datetime: ' + oyoDatetime
      }
      this.changeSearchRoute(1)
    },
    changeSearchRoute (page) {
      if (this.searchTextInput) {
        this.searchText = this.searchTextInput
        this.offset = parseInt((page - 1) * this.itemsPerPage)
        this.page = parseInt(this.offset/this.itemsPerPage) + 1
        this.$router.push('/result/' + Base64.encode(this.searchText) + '/' + this.offset)
        this.computeSearchArray()
        this.searchTheBlockchainForText()
        this.getMatchCount()
      }
    },
    getMatchCount () {
      let matchPhrase = this.searchText
      let queryMatch = {}
      queryMatch['$and'] = []
      while (matchPhrase.match(/s(?:[1-9]{1,2}):/)) {
        let match = matchPhrase.match(/(?:^|\s+)s([1-9]{1,2}):\s*(\S+)\s*/u)
        if (match && match[1] && match[2]) {
          var jsonVariable = {}
          jsonVariable['out.s' + match[1]] = match[2]
          queryMatch['$and'].push(jsonVariable)

          let regex = new RegExp( '(?:^|\\s+)s' + match[1] + ':\\s*\\S+\\s*', 'gu' )
          matchPhrase = matchPhrase.replace(regex, ' ')
        }
      }
      while (matchPhrase.match(/h(?:[1-9]|1[0-5]):\s*[0-9a-fA-F]+/)) {
        let match = matchPhrase.match(/(?:^|\s+)h([1-9]|1[0-5]):\s*([0-9a-fA-F]+)\s*/u)
        if (match && match[1] && match[2]) {
          var jsonVariable = {}
          jsonVariable['out.h' + match[1]] = match[2]
          queryMatch['$and'].push(jsonVariable)

          let regex = new RegExp( '(?:^|\\s+)h' + match[1] + ':\\s*[0-9a-fA-F]+\\s*', 'gu' )
          matchPhrase = matchPhrase.replace(regex, ' ')
        }
      }
      while (matchPhrase.match(/b(?:[1-9]|1[0-5]):\s*(?:[A-Za-z0-9+/]{4})*(?:[A-Za-z0-9+/]{2}==|[A-Za-z0-9+/]{3}=)?/)) {
        let match = matchPhrase.match(/(?:^|\s+)b([1-9]|1[0-5]):\s*((?:[A-Za-z0-9+/]{4})*(?:[A-Za-z0-9+/]{2}==|[A-Za-z0-9+/]{3}=)?)\s*/u)
        if (match && match[1] && match[2]) {
          var jsonVariable = {}
          jsonVariable['out.b' + match[1]] = match[2]
          queryMatch['$and'].push(jsonVariable)

          let regex = new RegExp( '(?:^|\\s+)b' + match[1] + ':\\s*(?:[A-Za-z0-9+/]{4})*(?:[A-Za-z0-9+/]{2}==|[A-Za-z0-9+/]{3}=)?\\s*', 'gu' )
          matchPhrase = matchPhrase.replace(regex, ' ')
        }
      }
      if (matchPhrase.includes("prefix:")) {
        let match = matchPhrase.match(/(?:^|\s+)prefix:\s*([0-9a-fA-F]+)\b\s*/u)
        if (match && match[1]) {
          queryMatch['$and'].push({'$or': [{'out.h1': match[1]}, {'$and': [{'$or': [{'out.b1': 'bWV0YQ=='}, {'out.b3': 'fA=='}]}, {'out.h4': match[1]}]}, {'$and': [{'out.b6': 'fA=='}, {'out.h7': match[1]}]}]})
          matchPhrase = matchPhrase.replace(/(?:^|\s+)prefix:\s*[0-9a-fA-F]+\b\s*/gu, ' ')
        }
      }
      if (matchPhrase.includes("prefix:")) {
        let match = matchPhrase.match(/(?:^|\s+)prefix:\s*([0-9a-zA-Z\.@]+)\b\s*/u)
        if (match && match[1]) {
          queryMatch['$and'].push({'$or': [{'out.s1': match[1]}, {'$and': [{'$or': [{'out.b1': 'bWV0YQ=='}, {'out.b3': 'fA=='}]}, {'out.s4': match[1]}]}, {'$and': [{'out.b6': 'fA=='}, {'out.s7': match[1]}]}]})
          matchPhrase = matchPhrase.replace(/(?:^|\s+)prefix:\s*[0-9a-zA-Z\.@]+\b\s*/gu, ' ')
        }
      }
      if (matchPhrase.includes("app:")) {
        let match = matchPhrase.match(/(?:^|\s+)app:\s*([0-9a-zA-Z\.@]+)\b\s*/u)
        if (match && match[1]) {
          queryMatch['$and'].push({'$or': [{'$and': [{'out.s9':'app'}, {'out.s10': match[1]}]}, {'$and': [{'out.s21':'app'}, {'out.s22': match[1]}]}, {'$and': [{'out.s23':'app'}, {'out.s24': match[1]}]}]})
          matchPhrase = matchPhrase.replace(/(?:^|\s+)app:\s*[0-9a-zA-Z\.@]+\b\s*/gu, ' ')
        }
      }
      if (matchPhrase.includes("from:")) {
        let match = matchPhrase.match(/(?:^|\s+)from:\s*(\S+)\s*/u)
        if (match && match[1]) {
          queryMatch['$and'].push({'in.e.a': window.convertAddress(match[1])})
          matchPhrase = matchPhrase.replace(/(?:^|\s+)from:\s*\S+\s*/gu, ' ')
        }
      }
      if (matchPhrase.includes("notfrom:")) {
        let match = matchPhrase.match(/(?:^|\s+)notfrom:\s*(\S+)\s*/u)
        if (match && match[1]) {
          queryMatch['$and'].push({'in.e.a': { '$ne': window.convertAddress(match[1]) }})
          matchPhrase = matchPhrase.replace(/(?:^|\s+)notfrom:\s*\S+\s*/gu, ' ')
        }
      }
      // datetime:
      let endTimestamp = parseInt(new Date().getTime() / 1000)
      if (matchPhrase.includes("datetime:")) {
        let match = matchPhrase.match(/(?:^|\s+)datetime:\s*(\S+)\s*/u)
        if (match && match[1]) {
          endTimestamp = this.oyoDatetimeToTimestamp(match[1])
          matchPhrase = matchPhrase.replace(/(?:^|\s+)datetime:\s*\S+\s*/gu, ' ')
        }
      }
      // period:
      let beginTimestamp = parseInt(endTimestamp - (window.periodDefault * 24 * 60 * 60))
      if (matchPhrase.includes("period:")) {
        let match = matchPhrase.match(/(?:^|\s+)period:\s*(\S+)\s*/u)
        if (match && match[1]) {
          beginTimestamp = parseInt(endTimestamp - (match[1] * 24 * 60 * 60))
          matchPhrase = matchPhrase.replace(/(?:^|\s+)period:\s*\S+\s*/gu, ' ')
        }
      }
      queryMatch['$and'].push({'blk.t': {'$gte': beginTimestamp, '$lte': endTimestamp}})

      //quoting words containing '.'
      matchPhrase = matchPhrase.trim()
      let tmpMatchPhraseArray = matchPhrase.match(/\S+|"[^"]+"/gu) || []
      for (let i = 0; i < tmpMatchPhraseArray.length; ++i) {
        if (tmpMatchPhraseArray[i].includes('.') && !tmpMatchPhraseArray[i].includes('"')) {
          tmpMatchPhraseArray[i] = '"' + tmpMatchPhraseArray[i] + '"'
        }
      }
      matchPhrase = tmpMatchPhraseArray.join(" ")

      if (matchPhrase) {
        queryMatch['$and'].push({'$text': {'$search': '' + matchPhrase + ''}})
      }
      let query = {
        'v': 3,
        'q': {
          'aggregate': [
            {'$match': queryMatch},
            {'$count': "count"}
          ]
        }
      }

      let b64 = Base64.encode(JSON.stringify(query))
      let url = bitdbNode + b64

      let header
      if (window.bitdbApiKey) {
        header = {
          headers: { key: window.bitdbApiKey }
        }
      }

      axios
        .get(url, header)
        .then(response => {
          if (response.data.c.length > 0) {
            this.matchesCount = response.data.c[0].count
          } else {
            this.matchesCount = 0
          }
        })
        .catch(error => {
          console.log(error)
          this.errored = true
        })
    },
    searchTheBlockchainForText () {
      this.loading = true
      this.errored = false
      // nullify cached results
      this.resultLongestStrings = []
      this.resultConcatinatedStrings = []

      let matchPhrase = this.searchText
      let queryMatch = {}
      queryMatch['$and'] = []
      while (matchPhrase.match(/s(?:[1-9]{1,2}):/)) {
        let match = matchPhrase.match(/(?:^|\s+)s([1-9]{1,2}):\s*(\S+)\s*/u)
        if (match && match[1] && match[2]) {
          var jsonVariable = {}
          jsonVariable['out.s' + match[1]] = match[2]
          queryMatch['$and'].push(jsonVariable)

          let regex = new RegExp( '(?:^|\\s+)s' + match[1] + ':\\s*\\S+\\s*', 'gu' )
          matchPhrase = matchPhrase.replace(regex, ' ')
        }
      }
      while (matchPhrase.match(/h(?:[1-9]|1[0-5]):\s*[0-9a-fA-F]+/)) {
        let match = matchPhrase.match(/(?:^|\s+)h([1-9]|1[0-5]):\s*([0-9a-fA-F]+)\s*/u)
        if (match && match[1] && match[2]) {
          var jsonVariable = {}
          jsonVariable['out.h' + match[1]] = match[2]
          queryMatch['$and'].push(jsonVariable)

          let regex = new RegExp( '(?:^|\\s+)h' + match[1] + ':\\s*[0-9a-fA-F]+\\s*', 'gu' )
          matchPhrase = matchPhrase.replace(regex, ' ')
        }
      }
      while (matchPhrase.match(/b(?:[1-9]|1[0-5]):\s*(?:[A-Za-z0-9+/]{4})*(?:[A-Za-z0-9+/]{2}==|[A-Za-z0-9+/]{3}=)?/)) {
        let match = matchPhrase.match(/(?:^|\s+)b([1-9]|1[0-5]):\s*((?:[A-Za-z0-9+/]{4})*(?:[A-Za-z0-9+/]{2}==|[A-Za-z0-9+/]{3}=)?)\s*/u)
        if (match && match[1] && match[2]) {
          var jsonVariable = {}
          jsonVariable['out.b' + match[1]] = match[2]
          queryMatch['$and'].push(jsonVariable)

          let regex = new RegExp( '(?:^|\\s+)b' + match[1] + ':\\s*(?:[A-Za-z0-9+/]{4})*(?:[A-Za-z0-9+/]{2}==|[A-Za-z0-9+/]{3}=)?\\s*', 'gu' )
          matchPhrase = matchPhrase.replace(regex, ' ')
        }
      }
      if (matchPhrase.includes("prefix:")) {
        let match = matchPhrase.match(/(?:^|\s+)prefix:\s*([0-9a-fA-F]+)\b\s*/u)
        if (match && match[1]) {
          queryMatch['$and'].push({'$or': [{'out.h1': match[1]}, {'$and': [{'$or': [{'out.b1': 'bWV0YQ=='}, {'out.b3': 'fA=='}]}, {'out.h4': match[1]}]}]})
//          queryMatch['$and'].push({'out.h1': match[1]})
          matchPhrase = matchPhrase.replace(/(?:^|\s+)prefix:\s*[0-9a-fA-F]+\b\s*/gu, ' ')
        }
      }
      if (matchPhrase.includes("prefix:")) {
        let match = matchPhrase.match(/(?:^|\s+)prefix:\s*([0-9a-zA-Z\.@]+)\b\s*/u)
        if (match && match[1]) {
          queryMatch['$and'].push({'$or': [{'out.s1': match[1]}, {'$and': [{'$or': [{'out.b1': 'bWV0YQ=='}, {'out.b3': 'fA=='}]}, {'out.s4': match[1]}]}]})
          matchPhrase = matchPhrase.replace(/(?:^|\s+)prefix:\s*[0-9a-zA-Z\.@]+\b\s*/gu, ' ')
        }
      }
      if (matchPhrase.includes("app:")) {
        let match = matchPhrase.match(/(?:^|\s+)app:\s*([0-9a-zA-Z\.@]+)\b\s*/u)
        if (match && match[1]) {
          queryMatch['$and'].push({'$or': [{'$and': [{'out.s9':'app'}, {'out.s10': match[1]}]}, {'$and': [{'out.s21':'app'}, {'out.s22': match[1]}]}, {'$and': [{'out.s23':'app'}, {'out.s24': match[1]}]}]})
          matchPhrase = matchPhrase.replace(/(?:^|\s+)app:\s*[0-9a-zA-Z\.@]+\b\s*/gu, ' ')
        }
      }
      if (matchPhrase.includes("from:")) {
        let match = matchPhrase.match(/(?:^|\s+)from:\s*(\S+)\s*/u)
        if (match && match[1]) {
          queryMatch['$and'].push({'in.e.a': window.convertAddress(match[1])})
          matchPhrase = matchPhrase.replace(/(?:^|\s+)from:\s*\S+\s*/gu, ' ')
        }
      }
      if (matchPhrase.includes("notfrom:")) {
        let match = matchPhrase.match(/(?:^|\s+)notfrom:\s*(\S+)\s*/u)
        if (match && match[1]) {
          queryMatch['$and'].push({'in.e.a': { '$ne': window.convertAddress(match[1]) }})
          matchPhrase = matchPhrase.replace(/(?:^|\s+)notfrom:\s*\S+\s*/gu, ' ')
        }
      }
      // datetime:
      let endTimestamp = parseInt(new Date().getTime() / 1000)
      this.searchUnixTimestamp = endTimestamp
      if (matchPhrase.includes("datetime:")) {
        let match = matchPhrase.match(/(?:^|\s+)datetime:\s*(\S+)\s*/u)
        if (match && match[1]) {
          endTimestamp = this.oyoDatetimeToTimestamp(match[1])
          this.searchUnixTimestamp = endTimestamp
          matchPhrase = matchPhrase.replace(/(?:^|\s+)datetime:\s*\S+\s*/gu, ' ')
        }
      }
      // period:
      let beginTimestamp = parseInt(endTimestamp - (window.periodDefault * 24 * 60 * 60))
      if (matchPhrase.includes("period:")) {
        let match = matchPhrase.match(/(?:^|\s+)period:\s*(\S+)\s*/u)
        if (match && match[1]) {
          this.searchPeriod = match[1]
          beginTimestamp = parseInt(endTimestamp - (match[1] * 24 * 60 * 60))
          matchPhrase = matchPhrase.replace(/(?:^|\s+)period:\s*\S+\s*/gu, ' ')
        }
      }
      queryMatch['$and'].push({'blk.t': {'$gte': beginTimestamp, '$lte': endTimestamp}})

      matchPhrase = matchPhrase.trim()

      //quoting words containing '.'
      let tmpMatchPhraseArray = matchPhrase.match(/\S+|"[^"]+"/gu) || []
      for (let i = 0; i < tmpMatchPhraseArray.length; ++i) {
        if (tmpMatchPhraseArray[i].includes('.') && !tmpMatchPhraseArray[i].includes('"')) {
          tmpMatchPhraseArray[i] = '"' + tmpMatchPhraseArray[i] + '"'
        }
      }
      matchPhrase = tmpMatchPhraseArray.join(" ")

      if (matchPhrase) {
        queryMatch['$and'].push({'$text': {'$search': '' + matchPhrase + ''}})
      }

      let query = {
        'v': 3,
        'q': {
          'find': queryMatch,
          'skip': parseInt(this.offset),
          'limit': this.itemsPerPage
        }
      }

      let b64 = Base64.encode(JSON.stringify(query))
      let url = bitdbNode + b64

      let header
      if (window.bitdbApiKey) {
        header = {
          headers: { key: window.bitdbApiKey }
        }
      }

      axios
        .get(url, header)
        .then(response => {
          this.resultData = response.data
          this.resultUnconfirmedItems = this.resultData.u
          this.resultConfirmedItems = this.resultData.c
          this.resultAllItems = this.resultUnconfirmedItems.concat(this.resultConfirmedItems)

          for (let i = 0; i < this.resultAllItems.length; ++i) {
            for (let j = 0; j < this.resultAllItems[i]['out'].length; ++j) {
              if (!this.resultAllItems[i]['out'][j]) {
                continue
              }

              //populate BitcomPrefix
              let prefix = this.resultAllItems[i]['out'][j].s1
              if (!(this.bitcomPrefixes.includes(prefix)) && this.protocols[prefix] === undefined) {
                try {
                  window.bchaddr.isLegacyAddress(prefix)
                  this.bitcomPrefixes.push(prefix)
                } catch (e) {
                }
              }

              //populate MapApps
              let thisOut = this.resultAllItems[i]['out'][j]
              if (thisOut.s9 === 'app' && thisOut.s10 !== undefined) {
                this.resultApps[i] = thisOut.s10
                continue
              }
              if (thisOut.s11 === 'app' && thisOut.s12 !== undefined) {
                this.resultApps[i] = thisOut.s12
                continue
              }
              if (thisOut.s13 === 'app' && thisOut.s14 !== undefined) {
                this.resultApps[i] = thisOut.s14
                continue
              }
              if (thisOut.s15 === 'app' && thisOut.s16 !== undefined) {
                this.resultApps[i] = thisOut.s16
                continue
              }
              if (thisOut.s17 === 'app' && thisOut.s18 !== undefined) {
                this.resultApps[i] = thisOut.s18
                continue
              }
              if (thisOut.s19 === 'app' && thisOut.s20 !== undefined) {
                this.resultApps[i] = thisOut.s20
                continue
              }
              if (thisOut.s21 === 'app' && thisOut.s22 !== undefined) {
                this.resultApps[i] = thisOut.s22
                continue
              }
              if (thisOut.s23 === 'app' && thisOut.s24 !== undefined) {
                this.resultApps[i] = thisOut.s24
                continue
              }
              if (thisOut.s25 === 'app' && thisOut.s26 !== undefined) {
                this.resultApps[i] = thisOut.s26
                continue
              }
              if (thisOut.s27 === 'app' && thisOut.s28 !== undefined) {
                this.resultApps[i] = thisOut.s28
                continue
              }
            }
          }
          this.getBitcomProtocols()
        })
        .catch(error => {
          console.log(error)
          this.errored = true
          this.loading = false
        })
    },

    getBitcomProtocols () {
      let queryMatch = {}
      queryMatch['$and'] = []
      queryMatch['$and'].push({'out.s1': '$'})
      queryMatch['$and'].push({'out.s2': 'echo'})
      queryMatch['$and'].push({'out.s4': 'to'})
      queryMatch['$and'].push({'out.s5': {'$in': ['name', 'url', 'tx_url']}})
      queryMatch['$and'].push({'in.e.a': {'$in': this.bitcomPrefixes}})

      let query = {
        'v': 3,
        'q': {
          'find': queryMatch,
          'skip': 0,
          'limit': 500
        }
      }
      let b64 = Base64.encode(JSON.stringify(query))
      let url = bitdbNode + b64

      let header
      if (window.bitdbApiKey) {
        header = {
          headers: { key: window.bitdbApiKey }
        }
      }

      axios
        .get(url, header)
        .then(response => {
          if (response.data !== undefined && (response.data.u !== undefined || response.data.c !== undefined)) {
            let bitcomEchoResults = []
            if (response.data.u !== undefined) {
              bitcomEchoResults = bitcomEchoResults.concat(response.data.u.reverse())
            }
            if (response.data.c !== undefined) {
              bitcomEchoResults = bitcomEchoResults.concat(response.data.c)
            }

            for (let i = 0; i < bitcomEchoResults.length; ++i) {
              for (let j = 0; j < bitcomEchoResults[i]['out'].length; ++j) {
                if (!bitcomEchoResults[i]['out'][j]) {
                  continue
                }
                if (this.bitcomProtocols[bitcomEchoResults[i]['in'][0].e.a] === undefined) {
                  this.bitcomProtocols[bitcomEchoResults[i]['in'][0].e.a] = {}
                }
                let field = bitcomEchoResults[i]['out'][j].s5
                if (this.bitcomProtocols[bitcomEchoResults[i]['in'][0].e.a][field] === undefined) {
                  this.bitcomProtocols[bitcomEchoResults[i]['in'][0].e.a][field] = bitcomEchoResults[i]['out'][j].s3
                }
              }
            }

            for (let key in this.bitcomProtocols) {
              if (!this.bitcomProtocols.hasOwnProperty(key)) continue;

              let bitcomProtocol = this.bitcomProtocols[key]
              if (this.protocols[key] === undefined) {
                let protocol = {}
                if (bitcomProtocol['name'] !== undefined) {
                  protocol['name'] = bitcomProtocol['name']
                }
                if (bitcomProtocol['tx_url'] !== undefined) {
                  protocol['link'] = [ bitcomProtocol['tx_url'], "var:tx.h" ]
                }
                if (protocol['name'] || protocol['link']) {
                  this.protocols[key] = protocol
                }
              }
            }

            this.loading = false
          }
        })
        .catch(error => {
          console.log(error)
          this.loading = false
        })
    },
    getLink (item, outIndex) {
      let link = ''
      let linkArray = []
      if (this.protocols[item.out[outIndex].h1] && this.protocols[item.out[outIndex].h1].link) {
        linkArray = this.protocols[item.out[outIndex].h1].link
      } else if (this.protocols[item.out[outIndex].s1] && this.protocols[item.out[outIndex].s1].link) {
        linkArray = this.protocols[item.out[outIndex].s1].link
      }
      for (let i = 0; i < linkArray.length; ++i) {
        if (linkArray[i].startsWith("var:")) {
          let tmpLink = linkArray[i].replace(/^var:/g, '')
          tmpLink = tmpLink.replace(/\?/g, outIndex)
          link += eval('item.' + tmpLink)
        } else {
          link += linkArray[i]
        }
      }
      if (link === '') {
        link = window.blockExplorerRpc + '/tx/' + item.tx.h
      }
      return link
    },
    getTitle (item, outIndex) {
      let title = ''
      let titleArray = []
      if (this.protocols[item.out[outIndex].h1] && this.protocols[item.out[outIndex].h1].title) {
        titleArray = this.protocols[item.out[outIndex].h1].title
      } else if (this.protocols[item.out[outIndex].s1] && this.protocols[item.out[outIndex].s1].title) {
        titleArray = this.protocols[item.out[outIndex].s1].title
      }
      for (let i = 0; i < titleArray.length; ++i) {
        if (titleArray[i].startsWith("var:")) {
          let tmpTitle = titleArray[i].replace(/^var:/g, '')
          tmpTitle = tmpTitle.replace(/\?/g, outIndex)
          title += eval('item.' + tmpTitle)
        } else {
          title += titleArray[i]
        }
      }
      return title
    },
    getProtocolName (item, outIndex) {
      let name = ''
      if (this.protocols[item.out[outIndex].h1] && this.protocols[item.out[outIndex].h1].name) {
        name = this.protocols[item.out[outIndex].h1].name
      } else if (this.protocols[item.out[outIndex].s1] && this.protocols[item.out[outIndex].s1].name) {
        name = this.protocols[item.out[outIndex].s1].name
      }
      return name
    },
    onSearch: function (searchTextInput) {
      this.searchTextInput = searchTextInput
      this.changeSearchRoute(1)
    }
  }
}
</script>

<style>
#all{
  color: #4285F4;
  font-weight: bold;
}
#profile_pic{
  width: 32px;
  height: 32px;
  border-radius: 100%;
}
.g_apps img{
  margin-left: 18px;
}
.s_results{
  grid-column: span 10;
  font-family: arial, sans-serif;
  margin: 15px 165px 51px;
}
.n_results{
  font-size: 13px;
  color: grey;
  margin-bottom: 15px;
}
.title{
  font-size: 18px;
  text-decoration: none;
  color: #1a0dab;
}
.title:visited{
  color: #609;
}
.title:hover{
  text-decoration: underline;
}
.s_url{
  margin:0;
  color: green;
  font-size: 14px;
  text-decoration: none;
}
.date{
  color: #808080;

}
.s_detail{
  margin-top: 0;
  font-size: 13px;
}
.s_result{
  margin-bottom: 30px;
}
.content{
  opacity: 0.8;
}
h3{
  font-family: arial, sans-serif;
  font-size: 18px;
  font-weight: normal;
}
.r_searches{
  display: flex;
  justify-content: right;
}
.r_searches ul{
  margin: 0;
}
.r_searches li{
  margin: 0 10px 10px 0;
}
.r_item{
  color: #1a0dab;
  font-size: 14px;
  font-weight: bold;
  text-decoration: none;
}
#l_right{
  margin-left: 30px;
}
.page_number{
  margin-top: 15px;
  margin-left: 147px;
}

.pagination {
    display: inline-block;
    vertical-align: top;
    width:100%;
    height:40px;
    padding-bottom: 10px;
}

.pagination .page-item {
    display: inline-block;
    text-align: center;
    font-size: small;
    padding: 0;
    width: 35px;
    text-decoration: none;
    color: black;
}

.pagination .prev-item {
    display: inline-block;
    text-align: center;
    font-size: small;
    padding: 0;
    width: 50px;
    text-decoration: none;
    color: black;
}

.pagination .next-item {
    display: inline-block;
    text-align: center;
    font-size: small;
    padding: 0;
    width: 50px;
    text-decoration: none;
    color: black;
}
.pagination a {
  text-decoration: none;
  color: #429BF9;
}
.pagination a:hover {
  text-decoration: underline;
}
.pagination .active-item a {
  color: #000000;
}
.showNextPeriod {
    display: inline-block;
    vertical-align: top;
    text-align: left;
    width:100%;
    height:40px;
    margin-left: 40px;
    padding-bottom: 10px;
}
.showNextPeriod a {
  text-decoration: none;
  color: #429BF9;
}
.showNextPeriod a:hover {
  text-decoration: underline;
}
.text_highlight {
  background: #FFFFFF;
  color: #000000;
  font-weight: bold;
}

@media screen and (max-width: 1024px) {
  .s_results{
    grid-column: span 10;
    font-family: arial, sans-serif;
    margin: 15px 45px 51px;
  }
  .page_number{
    margin-top: 15px;
    margin-left: 50px;
  }
}
@media screen and (max-width: 800px) {
  .s_results{
    grid-column: span 10;
    font-family: arial, sans-serif;
    margin: 15px 10px 51px;
  }
  .page_number{
    margin-top: 15px;
    margin-left: 10px;
  }
}
</style>



