// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
window.convertAddress = function convertAddress (address, outputFormat = null) {
  if (!address) {
    return
  }
  if ((window.bitdbAddressFormat === 'legacy' || outputFormat === 'legacy') && outputFormat !== 'cashaddr' && window.bchaddr.isCashAddress(address)) {
    return window.bchaddr.toLegacyAddress(address)
  }
  if ((window.bitdbAddressFormat === 'cashaddr' || outputFormat === 'cashaddr') && outputFormat !== 'legacy' && window.bchaddr.isLegacyAddress(address)) {
    return window.bchaddr.toCashAddress(address).substr(12)
  }
  return address
}

import Vue from 'vue'
import { Base64 } from 'js-base64'
import axios from 'axios'
import bitcore from 'bitcore-lib-cash'
import bitexplorer from 'bitcore-explorers'
import bchaddr from 'bchaddrjs'
import datacash from 'datacash'
import satoshiBitcoin from 'satoshi-bitcoin'
import CryptoJS from 'crypto-js'
import Paginate from 'vuejs-paginate'
import TextHighlight from 'vue-text-highlight'
import VueQriously from 'vue-qriously'
import VueHead from 'vue-head'
import _ from 'lodash'
import protocolsFile from './json/protocols.json'
import router from './router'
import App from './App'

window._ = _
window.axios = axios
window.Base64 = Base64
window.CryptoJS = CryptoJS
window.bitcore = bitcore
window.bitexplorer = bitexplorer
window.bchaddr = bchaddr
window.datacash = datacash
window.satoshiBitcoin = satoshiBitcoin

// default search period
window.periodDefault = 30

// protocols
window.protocolsDefault = protocolsFile
if (!localStorage.getItem('protocols')) {
  window.protocols = window.protocolsDefault
} else {
  window.protocols = localStorage.getItem('protocols')
}
// network name
window.networkNameDefault = 'BSV'
window.networkName = window.networkNameDefault
if (localStorage.getItem('networkName')) {
  if (window.networkNameDefault === localStorage.getItem('networkName'))
  {
    localStorage.removeItem('networkName')
  } else {
    window.networkName = localStorage.getItem('networkName')
  }
}

// bitdb genesis node
window.bitdbGenesisNodeDefault = 'https://genesis.bitdb.network/q/1FnauZ9aUH2Bex6JzdcV4eNX7oLSSEbxtN/'
window.bitdbBCHGenesisNodeDefault = 'https://bitdb.bitcoin.com/q/'
window.bitdbGenesisNode = window.bitdbGenesisNodeDefault
if (localStorage.getItem('bitdbGenesisNode')) {
  if (window.bitdbGenesisNodeDefault === localStorage.getItem('bitdbGenesisNode'))
  {
    localStorage.removeItem('bitdbGenesisNode')
  } else {
    window.bitdbGenesisNode = localStorage.getItem('bitdbGenesisNode')
  }
}
// bitdb babel node
window.bitdbNodeDefault = 'https://babel.bitdb.network/q/1DHDifPvtPgKFPZMRSxmVHhiPvFmxZwbfh/'
window.bitdbBCHNodeDefault = 'https://bitdb.bitcoin.com/q/'
window.bitdbNode = window.bitdbNodeDefault
if (localStorage.getItem('bitdbNode')) {
  if (window.bitdbNodeDefault == localStorage.getItem('bitdbNode'))
  {
    localStorage.removeItem('bitdbNode')
  } else {
    window.bitdbNode = localStorage.getItem('bitdbNode')
  }
}
// bitdb api key
window.bitdbApiKeyDefault = 'qpl84tsdqd6yxd3hpxhj5ngr3rp0pvlweqy7p7rzfy'
window.bitdbApiKey = window.bitdbApiKeyDefault
if (localStorage.getItem('bitdbApiKey')) {
  if (window.bitdbApiKeyDefault == localStorage.getItem('bitdbApiKey'))
  {
    localStorage.removeItem('bitdbApiKey')
  } else {
    window.bitdbApiKey = localStorage.getItem('bitdbApiKey')
  }
}
// bitdb address format
window.bitdbAddressFormatDefault = 'legacy'
window.bitdbBCHAddressFormatDefault = 'cashaddr'
window.bitdbAddressFormat = window.bitdbAddressFormatDefault
if (localStorage.getItem('bitdbAddressFormat')) {
  if (window.bitdbAddressFormatDefault == localStorage.getItem('bitdbAddressFormat'))
  {
    localStorage.removeItem('bitdbAddressFormat')
  } else {
    window.bitdbAddressFormat = localStorage.getItem('bitdbAddressFormat')
  }
}
// block explorer rpc
window.blockExplorerRpcDefault = 'https://bchsvexplorer.com'
window.blockExplorerBCHRpcDefault = 'https://cashexplorer.bitcoin.com'
window.blockExplorerRpc = window.blockExplorerRpcDefault
if (localStorage.getItem('blockExplorerRpc')) {
  if (window.blockExplorerRpcDefault == localStorage.getItem('blockExplorerRpc'))
  {
    localStorage.removeItem('blockExplorerRpc')
  } else {
    window.blockExplorerRpc = localStorage.getItem('blockExplorerRpc')
  }
}
// bitsocket node
window.bitsocketNodeDefault = 'https://babel.bitdb.network/s/1DHDifPvtPgKFPZMRSxmVHhiPvFmxZwbfh/'
window.bitsocketBCHNodeDefault = 'https://bitsocket.org/s/'
window.bitsocketNode = window.bitsocketNodeDefault
if (localStorage.getItem('bitsocketNode')) {
  if (window.bitsocketNodeDefault == localStorage.getItem('bitsocketNode'))
  {
    localStorage.removeItem('bitsocketNode')
  } else {
    window.bitsocketNode = localStorage.getItem('bitsocketNode')
  }
}
// results per page
window.resultsPerPageDefault = 20
window.resultsPerPage = window.resultsPerPageDefault
if (localStorage.getItem('resultsPerPage')) {
  if (parseInt(window.resultsPerPageDefault) == parseInt(localStorage.getItem('resultsPerPage')))
  {
    localStorage.removeItem('resultsPerPage')
  } else {
    window.resultsPerPage = localStorage.getItem('resultsPerPage')
  }
}

//correcting old addresses (2019.01.27). Remove in 3 months
if (localStorage.getItem('user.address')) {
  let address = localStorage.getItem('user.address')
  if (address.includes('bitcoincash')) {
    localStorage.setItem('user.address', address.substr(12))
  }
}
// moving user address to base58 (2019.01.27). Remove in 3 months
if (localStorage.getItem('user.address')) {
  let address = localStorage.getItem('user.address')
  if (window.bchaddr.isCashAddress(address)) {
    localStorage.setItem('user.address', window.convertAddress(address, 'legacy'))
  }
}

Vue.config.productionTip = false

Vue.use(VueQriously)
Vue.use(VueHead)

Vue.component('paginate', Paginate)
Vue.component('text-highlight', TextHighlight)

/* eslint-disable no-new */
window.vm = new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})

Vue.filter('striphtml', function (value) {
  let div = document.createElement('div')
  div.innerHTML = value
  let text = div.textContent || div.innerText || ''
  return text
})
Vue.filter('toTitle', function (text) {
  let resultString = text.split(' ')[0]
  if (text.includes(' ')) {
    resultString = resultString + ' ' + text.substr(text.indexOf(' ') + 1).split(/[.|:|;|-|!|?|<|&|(|)]+\s+/).slice(0, 1).join(' ').split(/\s+http/).slice(0, 1).join(' ').split(/\<br/).slice(0, 1).join(' ')
  }
  return resultString
})
Vue.filter('highlight', function(words, query) {
    return words.replace(query, '<span class="highlight">' + query + '</span>')
});



