<template>
          <!-- The Modal -->
          <div id="pinModal" class="modal">
            <!-- Modal content -->
          	<form @submit.prevent="enterPin()">
            <div class="modal-content">
              <span class="close" @click="closeModal('pinModal')">&times;</span>
              <h2>Pin</h2>

              <p style="color:#f11;font-size:small" v-if="errors.length">
                <b>Please correct the following error(s):</b>
                <ul class="errors">
                  <li v-for="error in errors">{{ error }}</li>
                </ul>
              </p>

              <p><input type="password" name="pin" v-model="pin" placeholder="Pin" autofocus/><br />
              <input type="checkbox" value="true" name="rememberPin" v-model="rememberPin"> Remember pin</p>
              <p>
                <input value="Login" type="submit">
                <input value="Close" type="button" @click="closeModal('pinModal')">
              </p>
            </div>
            </form>
          </div>
</template>


<script>
import walletMixin from '@/mixins/walletMixin'

  export default {
      name: 'PinModal',
      mixins: [walletMixin],
      data () {
        return {
          pin: null,
          rememberPin: null,
          errors: []
        }
      },
      methods: {
        enterPin () {
          this.errors = []
          if (this.rememberPin) {
            localStorage.setItem('user.pin', this.pin)
          } else {
            sessionStorage.setItem('user.pin', this.pin)
          }

          if (this.addressFromPrivateKey() === undefined || bchaddr.toCashAddress(this.getAddress()).substr(12) != this.addressFromPrivateKey().substr(12)) {
            this.errors.push('Incorrect pin')
            sessionStorage.removeItem('user.pin')
            localStorage.removeItem('user.pin')
            this.pin = ''
            return
          }

          this.$emit('pinEntered')
          this.closeModal('pinModal')
        }
      }
  };
</script>

<style>
</style>



