<template>
    <div class="grid">
      <HeaderNav  v-bind:title="'Log In / Sign Up'"/>

      <div class="s_results form">

      <form class="ui form" @submit.prevent="login">

      <div v-if="isNewUser">
        <p style="color:#f11;font-size:small">Your username, password and pin ARE your wallet. Use unique ones.</p>
        <p style="color:#f11;font-size:small">You don't need to backup your wallet. You can reproduce your wallet based on your username, password and pin.</p>
        <p style="color:#f11;font-size:small">Oyo.cash does not store any user data. We cannot recover your user data if you lose it.</p>
      </div>

        <div class="field-block">
          <h4>Username</h4>
          <input type="text" name="username" v-model="username" placeholder="Username" required autocomplete="off">
        </div>

        <div class="field-block">
          <h4>Password</h4>
          <input type="password" name="password" v-model="password" placeholder="Password" required autocomplete="off">
        </div>

        <div class="field-block">
          <h4>Pin code</h4>
          <input type="text" name="pin" v-model="pin" placeholder="Pin code" required autocomplete="off">
        </div>

        <div class="field-block">
          <input type="checkbox" value="true" name="rememberUser" v-model="rememberUser"> Remember username and password
        </div>

        <div class="form-buttons">
          <input value="Sign up" type="submit" v-if="isNewUser">
          <input value="Log in" type="submit" v-else>
          <p style="color:#666;font-size:small">Are you <a href="javascript:void(0)" @click="newUser()">new user</a>?</p>
          <p style="color:#666;font-size:small">Log in with <router-link to="/login/privatekey">Private Key</router-link></p>
        </div>
      </form>
      </div>
      <Footer/>
    </div>

</template>

<script>
import HeaderNav from '@/components/Header/Nav'
import Footer from '@/components/Footer'
import walletMixin from '@/mixins/walletMixin'

export default {
  name: 'LogInForm',
  mixins: [walletMixin],
  components: {
    HeaderNav,
    Footer
  },
  data () {
    return {
      isNewUser: false,
      username: null,
      password: null,
      pin: null,
      rememberUser: null,
      notification: {
        message: '',
        type: ''
      }
    }
  },
  head: function () {
    return {
      title: {
        inner: 'Create or login to Bitcoin Cash wallet'
      },
      // Meta tags
      meta: [
        { name: 'application-name', content: 'Oyo.cash' },
        { name: 'description', content: 'Create or login to Bitcoin Cash wallet using username and password. All in browser wallet', id: 'desc' },
        { name: 'twitter:title', content: 'Create or login to Bitcoin Cash wallet' },
        { n: 'twitter:description', c: 'Create or login to Bitcoin Cash wallet using username and password. All in browser wallet' },
        { itemprop: 'name', content: 'Create or login to Bitcoin Cash wallet' },
        { itemprop: 'description', content: 'Create or login to Bitcoin Cash wallet using username and password. All in browser wallet' },
        { property: 'og:title', content: 'Create or login to Bitcoin Cash wallet' }
      ]
    }
  },
  mounted () {
    if (this.loggedViewMode() || this.loggedSpendMode()) {
      this.$router.push('/')
    }
  },
  methods: {
    newUser () {
      this.isNewUser = true
    },
    login () {
      if (this.username && this.password && this.pin) {
        const userPasswordHash = bitcore.crypto.Hash.sha256(Buffer.from(this.username + this.password))
        const pinHash = bitcore.crypto.Hash.sha256(Buffer.from(this.pin))
        const buffArray = [userPasswordHash, pinHash]
        const hash = bitcore.crypto.Hash.sha256(Buffer.concat(buffArray))
        const bn = bitcore.crypto.BN.fromBuffer(hash)
        const pk = bitcore.PrivateKey(bn).toString()
        const address = bitcore.PrivateKey(bn).toAddress().toString()
        const userPasswordHashHex = userPasswordHash.toString('hex')
        if (this.rememberUser) {
          localStorage.setItem('user.address', window.convertAddress(address.substr(12), 'legacy'))
          localStorage.setItem('user.userPasswordHash', userPasswordHashHex)
        } else {
          sessionStorage.setItem('user.address', window.convertAddress(address.substr(12), 'legacy'))
          sessionStorage.setItem('user.userPasswordHash', userPasswordHashHex)
        }
        sessionStorage.setItem('user.pin', this.pin)

        // redirect to user home
        this.$router.push('/')
      } else {
        this.username = this.password = this.pin = ''
      }
    }
  }
}
</script>



