<template>

     		<li v-if="logged" class="dropdown" style="float: right;">
          <a href="javascript:void(0)" class="signinbox dropbtn" @click="showProfileNav = !showProfileNav">{{ getAddress().substr(0,8) }}...</a>
            <ul v-bind:class="{ 'nav-dropdown': !showProfileNav, 'nav-dropdown-active': showProfileNav }">
              <li><a href="javascript:void(0)">Balance: {{ balance }} sat</a></li>
              <li><router-link to="/wallet/receive">Receive {{ networkName || 'BSV' }}</router-link></li>
              <li><router-link to="/wallet/send">Send {{ networkName || 'BSV' }}</router-link></li>
              <li><router-link to="/wallet/export">Export wallet key</router-link></li>
              <li><a href="/" @click="logout">Logout</a></li>
            </ul>
        </li>
     		<li v-else style="float: right;"><router-link to="/login" class="signinbox">Sign in</router-link></li>
</template>


<script>
  import walletMixin from '@/mixins/walletMixin'

  export default {
      name: 'UserNav',
      mixins: [walletMixin],
      data () {
        return {
          networkName: window.networkName,
          showProfileNav: false
        }
      },
      props: {
      },
      mounted () {
      },
      computed: {
        logged: function () {
          if (this.loggedViewMode() || this.loggedSpendMode()) {
            return true
          }
          return false
        },
        balance: function () {
          if (this.addressInfo) {
            return this.addressInfo.balanceSat
//            return this.addressInfo.balanceSat + this.addressInfo.unconfirmedBalanceSat
          }
          return 0
        }
      },
      methods: {
      }
  };
</script>

<style>
</style>



